const styles = theme => ({
  root: {
    "& .MuiTableCell-head": {
      fontSize: "0.95rem"
    }
  },
  paper: {
    margin: theme.spacing(-8.9),
    padding: theme.spacing(6)
  },

  grid: {
    paddingLeft: theme.spacing(2)
  },
  td: {
    textIndent: "50px",
  },
  tr: {
    backgroundColor: "primary",
    color: "white"
  },
  t: {
    width: "200px"
  }

})



const CadeteSalesData = ({ classes, ...props }) => {
  const group = () => {

  }
  function groupByKey(array, key) {
    return array
      .reduce((hash, obj) => {
        if (obj[key] === undefined) return hash;
        return Object.assign(hash, { [obj[key]]: (hash[obj[key]] || []).concat(obj) })
      }, {})
  }




  var groupedSales = groupByKey(props.cTSales, 'cadete');
  //  console.log(groupedSales);

  //var dateGSales = groupByDate(groupedSales, "dateIn");
  // console.log(groupedSales);
  // console.log(props.cSaleList);
  var gTotalPrice = 0;
  var gDeposit = 0;
  var gTotal = 0;
  var gQuantity = 0;

  var result = [];
  function groupNSum(sDet) {
    //  console.log(sDet);

    sDet.forEach(function (a) {
      if (!this[a.prodName] && !this[a.cadete]) {
        this[a.prodName] = { date: a.date, prodName: a.prodName, quantity: 0, unitPrice: a.unitPrice, totalPrice: 0, deposit: 0, total: 0, cadete: a.cadete };
        result.push(this[a.prodName]);
      }
      this[a.prodName].quantity += a.quantity;
      this[a.prodName].totalPrice += a.totalPrice;
      this[a.prodName].deposit += a.deposit;
      this[a.prodName].total += a.total;
    }, Object.create(null));
    // console.log(result)
    gTotalPrice = (result.reduce((gTotalPrice, currentItem) => gTotalPrice = gTotalPrice + currentItem.totalPrice, 0));
    gDeposit = (result.reduce((gDeposit, currentItem) => gDeposit = gDeposit + currentItem.deposit, 0));
    gTotal = (result.reduce((gTotal, currentItem) => gTotal = gTotal + currentItem.total, 0));
    gQuantity = (result.reduce((gQuantity, currentItem) => gQuantity = gQuantity + currentItem.quantity, 0));
   
  }
  
  function resetR(){
    result =[];
  }


  const componentRef = useRef();
  return (

    <>

      <ReactToPrint
        trigger={() => <Controls.Button
          text="Print"
          variant="outlined"
          // startIcon={<AddIcon />}
          className={classes.newButton}
        // onClick={handlePrint} 
        />
        }
        content={() => componentRef.current}

      />


      <div >
        <div ref={componentRef} className='p-5'>
          <header>

            <div className="logo">
              {/* <img src="../images/genericlogo.jpg" alt="generic business logo" height={181} width={167} /> */}
              <h5>Jamumbi Enterprises</h5>
            </div>
            <div className="invoiceNbr">
              CADETE SALES REPORT
              <br />
              <h5> For the period: {props.dateR}</h5>
            </div>
          </header>
          {/* <div className="fromto from">
          <div className="panel">FROM:</div>
          <div className="fromtocontent">
            <span>Robert Crowley</span><br />
            <span>123 My St.</span><br />
            <span>Portland ME, 04101</span><br />
          </div>
        </div>
        <div className="fromto to">
          <div className="panel">TO:</div>
          <div className="fromtocontent">
            <span>Someone</span><br />
            <span>123 Street St.</span><br />
            <span>Portland ME, 04101</span>
          </div>
        </div> */}
          <section >

            {/* your favorite templating/data-binding library would come in handy here to generate these rows dynamically !*/}
            <table class="table  .table-responsive{-sm|-md|-lg|-xl|-xxl} table-bordered table-hover table-sm table-striped" >
              <thead class="table-dark" >
                <tr >
                  <th scope="col" className={classes.t}>Cadete</th>

                  <th scope="col">Product Details</th>


                </tr>
              </thead>
              <tbody>




                {/* {
                                    // console.log(plList),
                                    props.cSaleList.map((record, index) => {
                                         return ( <tr key = {index} hover>
                                             <td className={classes.td}>{record.prodName}</td>
                                             <td>{record.quantity}</td>
                                             <td>{record.unitPrice}</td>
                                             <td>{record.totalPrice}</td>
                                             <td>{record.deposit}</td>
                                             <td>{record.total}</td>
                                         </tr>
                                        

                                         )
                                     })
                                 }
                                <tr>
                                <td></td>
                                   <td className={classes.td}></td>
                                   <td></td>
                                   <td></td>
                                   <td></td>
                                   <td></td>
                                </tr>
                                 <tr>
                                   <td>Totals</td>
                                   <td>{gQuantity}</td>
                                   <td></td>
                                   <td>{gTotalPrice}</td>
                                   <td>{gDeposit}</td>
                                   <td>{gTotal}</td>

                                 </tr> */}

                {
                  // console.log(plList),
                  Object.keys(groupedSales).map((sale, index) => {
                    return (<tr key={index} hover>
                      <td className={classes.td}>{sale}</td>

                      <tr >
                        <th className={classes.t}>Name</th>
                        <th className={classes.t}>Quantity</th>
                        <th className={classes.t}>Unit Price</th>
                        <th className={classes.t}>Total Price</th>
                        <th className={classes.t}>Deposit</th>
                        <th className={classes.t}>Total</th>
                        <th > {groupNSum(groupedSales[sale])}</th>
                      </tr>

                      {result.map(sDet => (
                        // groupNSum(sDet)
                        <tr>

                          <td className={classes.t}>{sDet.prodName}</td>
                          <td className={classes.t}>{sDet.quantity}</td>
                          <td className={classes.t}>{sDet.unitPrice}</td>
                          <td className={classes.t}>{sDet.totalPrice}</td>
                          <td className={classes.t}>{sDet.deposit}</td>
                          <td className={classes.t}>{sDet.total}</td>
                        </tr>
                      ))}

                      {
                        <tr>
                          <th>Total</th>
                          <td>{gQuantity}</td>
                          <td></td>
                          <td>{gTotalPrice}</td>
                          <td>{gDeposit}</td>
                          <td>{gTotal}</td>
                          <td>{resetR()}</td>
                        </tr>
                      }
                    </tr>


                    )

                  })


                }






              </tbody>
            </table>


          </section>
        </div>


      </div>
    </>


  );
}

//export default staff; 

const mapStateToProps = state => ({
  productsList: state.products.list,
  creditSalesList: state.creditSale.list,
  tcTransactionsList: state.trucks_cadetes.list,
  nSTransactionDeatail: state.cash.list,
  salesList: state.cashSale.list,
  cSaleDetailsList: state.credit.list,
  tcTransDetailsList: state.trucks.list,
  expensesList: state.expenses.list,
  companyDataList: state.companyData.list,
})

const mapActionToProps = {

  fetchAllProducts: productActions.fetchAllProduct,
  fetchAllNSTransactions: cashSalesActions.fetchAllTransactions,
  fetchAllTCTransactions: TCActions.fetchAllTransactions,
  fetchAllCSTransactions: creditSalesActions.fetchAllTransactions,
  fetchAllTransactionDetails: cashSalesActions.fetchAllTransactionDetails,
  fetchAllCSTransactionDetails: creditSalesActions.fetchAllTransactionDetails,
  fetchAllTCTransactionDetails: TCActions.fetchAllTransactionDetails,
  fetchAllExpenses: Eactions.fetchAllExpenses,
  fetchAllCompanyData: companyDataActions.fetchAllCompanyData,

}

export default connect(mapStateToProps, mapActionToProps)(withStyles(styles)(CadeteSalesData));




