import NotListedLocation from '@material-ui/icons/NotListedLocation';
import * as productActions from "../../../actions/Products/products";
import * as cashSalesActions from "../../../actions/CashSale/cashSale";
import * as creditSalesActions from "../../../actions/CreditSale/creditSale";
import * as TCActions from "../../../actions/Trucks_Cadetes/Trucks_Cadetes";
import Controls from "../../Components/controls/Controls";
