import * as actions from "../../../actions/Staff/addStaff";


const styles = theme => ({
  root: {
    "& .MuiTableCell-head": {
      fontSize: "0.95rem"
    }
  },
  paper: {
    margin: theme.spacing(-8.9),
    padding: theme.spacing(6)
  },

  grid: {
    paddingLeft: theme.spacing(2)
  },
  td: {
    textIndent: "50px",
  },
  tr: {
    backgroundColor: "primary",
    color: "white"
  }

})



const CadeteSales = ({ classes, ...props }) => {
  //Toast message
  const { addToast } = useToasts();
  const [currentId, setCurrentId] = useState(0);
  const [confirmDialog, setConfirmDialog] = useState(false);
  const [plList, setPlList] = useState([{}]);
  const [nSCost, setNSCost] = useState([]);
  const [expD, setExpD] = useState([]);
  const [dateR, setDateR] = useState(null);
  const [compD, setCompD] = useState("");
  const [clicked, setClicked] = useState(false);
  const componentRef = useRef();
  const [nSaleList, setNSaleList] = useState([]);
  const [cSaleList, setCSaleList] = useState([{}]);
  const [cTSales, setCTSales] = useState([{}]);

  var dateRange = "";
  var today = new Date();
  var dd = String(today.getDate()).padStart(2, '0');
  var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
  var yyyy = today.getFullYear();

  today = yyyy + '-' + mm + '-' + dd;
  // console.log(today);
  const startValue: Date = new Date(new Date(new Date().getFullYear(), new Date().getMonth(), dd));
  const endValue: Date = new Date(new Date(new Date().getFullYear(), new Date().getMonth() + 1, dd));
  const minDate: Date = new Date(new Date(new Date().getFullYear(), new Date().getMonth(), 8));
  const maxDate: Date = new Date(new Date(new Date().getFullYear(), new Date().getMonth(), dd));
  useEffect(() => {

    props.fetchAllProducts();
    props.fetchAllNSTransactions();
    props.fetchAllTCTransactions();
    props.fetchAllCSTransactions();
    props.fetchAllTransactionDetails();
    props.fetchAllCSTransactionDetails();
    props.fetchAllTCTransactionDetails();
    props.fetchAllExpenses();
    props.fetchAllCompanyData();
    props.fetchAllCustomers();
    props.fetchAllAddStaff();
  
  }, [])

  const showToast = () => {
    addToast("Record was deleted succesffuly", { appearance: 'error' })
    addToast("Record was deleted succesffuly", { appearance: 'error' })
    addToast("Record was deleted succesffuly", { appearance: 'error' })

  }
  const onDelete = id => {
    props.deleteCategory(id, showToast);
    setConfirmDialog(false)

  }

  const openInDialog = () => {
    setConfirmDialog(true)



  }

  const calcPl = () => {
    try {
   
      //2. Paid Credit sale transactions
      //filter transactions by date
      var tCTranslist = props.tcTransactionsList.filter(x => x.returnDate.toString().substring(0, 10) >= dateR.toString().substring(0, 10) && x.returnDate.toString().substring(0, 10) <= dateR.toString().substring(13, 23));
      // console.log(tCTranslist);
      var cScost = 0;
      var tCTransCode = "";

      var TSaleDetails = [];
      var newTSaleDetails = [];

      tCTranslist.forEach((tCSale) => {

        tCTransCode = tCSale.transCode;
        //get transdetails by transcode
        var tCTransDetail = props.tcTransDetailsList.filter(x => x.transCode == tCTransCode);
        // console.log(tCTransDetail);
        var cadetes = props.addStaffList.find(x => x.id == tCSale.cadeteId)
        // console.log(cadetes);
        tCTransDetail.forEach((tCDetail) => {
          var prodId = tCDetail.productId;
          var prod = props.productsList.find(x => x.productId == prodId);

          // cScost = cScost + prod.buyingPrice * cSDetail.quantity;

          //get products details
          const tCSaleDetails = {
            cadete: cadetes.fName + " " + cadetes.sName,
            productId: prod.productId,
            prodName: prod.name,
            quantity: tCDetail.quantity,
            unitPrice: prod.sellingPrice,
            totalPrice: tCDetail.totalPrice,
            deposit: tCDetail.deposit,
            total: tCDetail.total,
            date: tCSale.dateIn
          }
          newTSaleDetails = [...TSaleDetails, tCSaleDetails];
          TSaleDetails = newTSaleDetails;
          // console.log(cSDetail.productId);
          // console.log(cSDetail.quantity);
          //  console.log(newCSaleDetails);
        });
      });

      var result = [];

      newTSaleDetails.forEach(function (a) {
        if (!this[a.prodName] && !this[a.cadete]) {
          this[a.cadete] = { date: a.date, prodName: a.prodName, quantity: 0, unitPrice: a.unitPrice, totalPrice: 0, deposit: 0, total: 0, cadete: a.cadete };
          result.push(this[a.cadete]);
        }
        this[a.cadete].quantity += a.quantity;
        this[a.cadete].totalPrice += a.totalPrice;
        this[a.cadete].deposit += a.deposit;
        this[a.cadete].total += a.total;
      }, Object.create(null));

      // console.log(result);

      // console.log(result);
      // setNSaleList(result);
      setCTSales(newTSaleDetails);

      setCompD(props.companyDataList);

    } catch (error) {

      console.log(error.message);
    }


  }

  const onChange = (e) => {
    dateRange = e.text;
    setDateR(dateRange);
  }; // the event handler



  const handlePrint = useReactToPrint({
    content: () => componentRef.current
  });

  return (
    <>
      {/* <PageHeader
        title="Categories"
        //subTitle="Form design with validation"
        icon={<CategoryTwoToneIcon fontSize="large" />}
      />, */}
      <Paper className={classes.paper} elevation={3}  >
        <Grid container>
          <Grid item xs={12}>

            {/* <AddNewCategoryForm {...({ currentId, setCurrentId })} /> */}
            <Toolbar>
              {/* <Controls.Input
                className={classes.searchInput}
                label="Search Customer"

                InputProps={{
                  startAdornment: (<InputAdornment position="start">
                    <Search />
                  </InputAdornment>)
                }}
                onChange={handleSearch}

              /> */}
              <DateRangePickerComponent

                placeholder="Enter Date Range"
                startDate={startValue}
                endDate={endValue}
                // min={minDate}
                max={maxDate}
                // minDays={3}
                // maxDays={5}
                format="yyyy-MM-dd"
                start="Year"
                date="Year"
                change={onChange}
              // style={{ width: '50%' }}
              >


              </DateRangePickerComponent>
              <Controls.Button
                text="Load "
                variant="outlined"
                // startIcon={<AddIcon />}
                className={classes.newButton}
                onClick={() => { calcPl(); setClicked(true) }}
              />
            </Toolbar>

            <Toolbar>


              {/* <Controls.Button
                text="Print"
                variant="outlined"
                startIcon={<AddIcon />}
                className={classes.newButton}
                onClick={handlePrint} /> */}

              {/* <ReactToPrint
                trigger={() => <Controls.Button
                  text="Print"
                  variant="outlined"
                  startIcon={<AddIcon />}
                  className={classes.newButton}
                // onClick={handlePrint} 
                />
                }
                content={() => componentRef.current}

              /> */}
            </Toolbar>
          </Grid>
          <Grid>

          </Grid>
          <Grid item xs={12}>
            {/* your favorite templating/data-binding library would come in handy here to generate these rows dynamically !*/}
            <div>

              {clicked === true && <AllPaidCreditSalesReportDataCus {...({ currentId, setCurrentId, plList, setPlList, expD, setExpD, dateR, setDateR, compD, setCompD, nSaleList, setNSaleList, cSaleList, setCSaleList, cTSales, setCTSales })} />}
            </div>


          </Grid>
        </Grid>
      </Paper>

      <ConfirmDialog
        title="Are you sure you want to delete this record?"
        subTitle="This operation can't be reversed"
        confirmDialog={confirmDialog}
        setConfirmDialog={setConfirmDialog}
      />
    </>
  );
}

//export default staff; 

const mapStateToProps = state => ({
  productsList: state.products.list,
  creditSalesList: state.creditSale.list,
  tcTransactionsList: state.trucks_cadetes.list,
  nSTransactionDetail: state.cash.list,
  salesList: state.cashSale.list,
  cSaleDetailsList: state.credit.list,
  tcTransDetailsList: state.trucks.list,
  expensesList: state.expenses.list,
  companyDataList: state.companyData.list,
  customersList: state.customers.list,
  addStaffList: state.addStaff.list,
})

const mapActionToProps = {

  fetchAllProducts: productActions.fetchAllProduct,
  fetchAllNSTransactions: cashSalesActions.fetchAllTransactions,
  fetchAllTCTransactions: TCActions.fetchAllTransactions,
  fetchAllCSTransactions: creditSalesActions.fetchAllTransactions,
  fetchAllTransactionDetails: cashSalesActions.fetchAllTransactionDetails,
  fetchAllCSTransactionDetails: creditSalesActions.fetchAllTransactionDetails,
  fetchAllTCTransactionDetails: TCActions.fetchAllTransactionDetails,
  fetchAllExpenses: Eactions.fetchAllExpenses,
  fetchAllCompanyData: companyDataActions.fetchAllCompanyData,
  fetchAllCustomers: customerActions.fetchAllCustomer,
  fetchAllAddStaff: actions.fetchAll,
}

export default connect(mapStateToProps, mapActionToProps)(withStyles(styles)(CadeteSales));









